import { supabase } from '../lib/supabase'

// Initialize local storage for maintenance records if it doesn't exist
const initializeLocalStorage = () => {
  if (typeof window !== 'undefined') {
    if (!localStorage.getItem('maintenance_records')) {
      localStorage.setItem('maintenance_records', JSON.stringify([]));
    }
  }
};

export const maintenanceService = {
  // Get all maintenance records
  getMaintenanceRecords: async () => {
    try {
      initializeLocalStorage();
      // Try to get from local storage first
      if (typeof window !== 'undefined') {
        const records = JSON.parse(localStorage.getItem('maintenance_records') || '[]');
        return records;
      }
      
      // Fallback to Supabase if localStorage is not available
      const { data, error } = await supabase
        .from('maintenance_records')
        .select(`
          *,
          vehicles:vehicle_id(plate, model),
          technicians:technician_id(name)
        `)
        .order('date', { ascending: false });
      
      if (error) throw error;
      return data;
    } catch (error) {
      console.error('Error getting maintenance records:', error);
      return [];
    }
  },

  // Get maintenance record by ID with parts
  getMaintenanceById: async (id) => {
    try {
      initializeLocalStorage();
      // Try to get from local storage first
      if (typeof window !== 'undefined') {
        const records = JSON.parse(localStorage.getItem('maintenance_records') || '[]');
        const record = records.find(r => r.id === id);
        return record || null;
      }
      
      // Fallback to Supabase
      const { data, error } = await supabase
        .from('maintenance_records')
        .select(`
          *,
          vehicles:vehicle_id(plate, model),
          technicians:technician_id(name),
          maintenance_parts(
            parts:part_id(
              id, 
              name, 
              part_categories:category_id(name)
            )
          )
        `)
        .eq('id', id)
        .single();
      
      if (error) throw error;
      return data;
    } catch (error) {
      console.error('Error getting maintenance by ID:', error);
      return null;
    }
  },

  // Create a new maintenance record
  createMaintenanceRecord: async (record, parts) => {
    try {
      initializeLocalStorage();
      
      // Generate a random ID for the new record
      const id = 'local_' + Math.random().toString(36).substr(2, 9);
      
      // Create the new record with the ID
      const newRecord = {
        id,
        ...record,
        // Include relationships data for easier display
        vehicles: {
          plate: record.vehiclePlate || "",
          model: ""
        },
        technicians: {
          name: record.technician || ""
        },
        // Store selected parts
        parts: parts || []
      };
      
      // Add to local storage
      if (typeof window !== 'undefined') {
        const records = JSON.parse(localStorage.getItem('maintenance_records') || '[]');
        records.push(newRecord);
        localStorage.setItem('maintenance_records', JSON.stringify(records));
        return newRecord;
      }
      
      // Fallback to Supabase if localStorage is not available
      const { data, error } = await supabase
        .from('maintenance_records')
        .insert([record])
        .select();
      
      if (error) throw error;
      
      // If parts exist, add them to the maintenance_parts table
      if (parts && parts.length > 0) {
        const partsToInsert = parts.map(partId => ({
          maintenance_id: data[0].id,
          part_id: partId
        }));
        
        const { error: partsError } = await supabase
          .from('maintenance_parts')
          .insert(partsToInsert);
        
        if (partsError) throw partsError;
      }
      
      return data[0];
    } catch (error) {
      console.error('Error creating maintenance record:', error);
      throw error;
    }
  }
} 